@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Courier New", monospace;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    max-width: 770px;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 26px;
  }
}

.btn {
  position: relative;
  display: inline-flex;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .btn:hover {
    opacity: 0.6;
  }
}
.btn.focus-visible:focus:not(:active) {
  outline: 3px solid #00b2ff;
}
.btn.focus-visible:active {
  opacity: 0.2;
}
.btn:disabled, .btn.disabled {
  opacity: 0.6;
  pointer-events: none;
}

.title {
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-weight: 400;
}

.header {
  position: sticky;
  top: 0;
  min-height: 120px;
  padding-top: 30px;
  padding-bottom: 35px;
  color: #f2f5f6;
  background-image: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (max-width: 1023px) {
  .header {
    position: static;
    min-height: 100px;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 70px;
    padding-top: 15px;
    padding-bottom: 20px;
  }
}

.header__container {
  display: grid;
  grid-template-columns: minmax(min-content, 355px) 480px 1fr 50px;
  gap: 10px;
  align-items: center;
}
@media (max-width: 1023px) {
  .header__container {
    display: flex;
    justify-content: space-between;
  }
}

.header__logo {
  grid-column: 1/2;
}

.header__address {
  grid-column: 2/3;
}

.header__btn {
  grid-column: 3/4;
  justify-self: end;
  min-width: 160px;
  min-height: 40px;
  padding: 11px 15px;
  font-size: 14px;
  line-height: 16px;
  color: #f2f5f6;
  border: 1px solid #f2f5f6;
}
@media (max-width: 1023px) {
  .header__btn {
    display: none;
  }
}

.logo {
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .logo:hover {
    opacity: 0.6;
  }
}
.logo.focus-visible:focus:not(:active) {
  outline: 2px solid #00b2ff;
}
.logo.focus-visible:active {
  opacity: 0.2;
}

.contacts {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  column-gap: 20px;
}
@media (max-width: 1023px) {
  .contacts {
    display: flex;
    margin-right: 20px;
    align-self: start;
  }
}
@media (max-width: 767px) {
  .contacts {
    margin-right: 9px;
  }
}

.contacts__link {
  display: flex;
  align-items: center;
  font-style: normal;
  color: inherit;
  transition: opacity 0.2s ease-out;
}
@media (max-width: 1023px) {
  .contacts__link {
    width: 40px;
    height: 40px;
    padding: 10px;
  }
}
@media (hover: hover) {
  .contacts__link:hover {
    opacity: 0.6;
  }
}
.contacts__link.focus-visible:focus:not(:active) {
  outline: 2px solid #00b2ff;
}
.contacts__link.focus-visible:active {
  opacity: 0.2;
}

.contacts__icon {
  flex-shrink: 0;
  color: #00b2ff;
}

.contacts__txt {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .contacts__txt {
    display: none;
  }
}

.footer {
  margin-top: auto;
  padding-top: 50px;
  padding-bottom: 55px;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
  background-color: #041827;
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr 470px 340px;
  gap: 30px;
}

.footer__logo {
  display: block;
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  .footer__logo {
    margin-bottom: 15px;
  }
}

.footer__column {
  display: flex;
  flex-direction: column;
  align-self: stretch;
}

.footer__title {
  margin-top: 5px;
  margin-bottom: 20px;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-transform: uppercase;
}

.footer__nav {
  flex-grow: 1;
}

.footer__list {
  height: 100%;
}

.footer__list * + * {
  margin-top: 10px;
}

.footer__address {
  font-style: normal;
}

.footer__address p {
  margin: 0;
}

.footer__link {
  display: block;
  color: inherit;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .footer__link:hover {
    opacity: 0.6;
  }
}
.footer__link.focus-visible:focus:not(:active) {
  outline: 3px solid #00b2ff;
}
.footer__link.focus-visible:active {
  opacity: 0.2;
}
.footer__link:disabled, .footer__link.disabled {
  opacity: 0.6;
  pointer-events: none;
}

.social-list {
  display: flex;
  justify-content: space-between;
  width: min-content;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.social-list__item + .social-list__item {
  margin-left: 15px;
}

.social-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  color: #f8fdff;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .social-list__link:hover {
    opacity: 0.6;
  }
}
.social-list__link.focus-visible:focus:not(:active) {
  outline: 3px solid #00b2ff;
}
.social-list__link.focus-visible:active {
  opacity: 0.2;
}
.social-list__link:disabled, .social-list__link.disabled {
  opacity: 0.6;
  pointer-events: none;
}

.footer-nav__list {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(4, min-content);
  gap: 10px 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  line-height: 20px;
}

.footer-nav li {
  height: min-content;
}

.footer-nav__link {
  display: block;
  color: inherit;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .footer-nav__link:hover {
    opacity: 0.6;
  }
}
.footer-nav__link.focus-visible:focus:not(:active) {
  outline: 3px solid #00b2ff;
}
.footer-nav__link.focus-visible:active {
  opacity: 0.2;
}
.footer-nav__link:disabled, .footer-nav__link.disabled {
  opacity: 0.6;
  pointer-events: none;
}

